    border-radius: 5px;
    flex-basis: 25%;
    color: #20253A;
    padding: 2rem;
    flex-basis: 30%;
}

.score h2{
    font-size: 1.5rem;
    font-weight: 500;
}

.score_chart{
    background-color: white;
}